// arraytargetberofe. before===null
export function reorder_array<T>(array: Array<T>, target: T, before: T | null): Array<T>;
export function reorder_array<T, U>(
  array: Array<T>,
  target: U,
  before: U | null,
  func: (e: T, t: U) => boolean,
): Array<T>;
export function reorder_array<T, U>(
  array: Array<T>,
  target: U,
  before: U | null,
  func?: (e: T, t: U) => boolean,
): Array<T> {
  if (func === undefined) func = (e, t) => e === (t as any as T); // func1
  const _func = func as Exclude<typeof func, undefined>; // funcundefined
  const target_elem = array.find((e) => _func(e, target));
  if (target_elem === undefined) return array;
  const result = array.filter((e) => !_func(e, target));
  const index = before !== null ? result.findIndex((e) => _func(e, before)) : result.length;
  if (index === -1) return array; // before
  result.splice(index, 0, target_elem);
  return result;
}
// objectmap
export const map_object = <K extends string | number | symbol, V, R>(
  obj: Record<K, V>,
  callbackfn: (value: V, key: K, object: Record<K, V>) => R,
): R[] => {
  return (Object.keys(obj) as K[]).map((k) => callbackfn(obj[k], k, obj));
};
export const map_to_object = <V, RK extends string | number | symbol, RV>(
  arr: Array<V>,
  callbackfn: (value: V, index: number, arr: Array<V>) => Record<RK, RV>,
): Record<RK, RV> =>
  arr.reduce<Record<RK, RV>>((acm, v, i, arr) => ({ ...acm, ...callbackfn(v, i, arr) }), {} as Record<RK, RV>);
